body{width:100vw;height:100vh;font-size:16px;background:no-repeat 50% url(/assets/warrior_4096.jpg);background-size:cover;display:flex;flex-direction:column;justify-content:center;align-items:center;color:#fff}@media screen and (max-width:2880px){body{background-image:url(/assets/warrior_2880.jpg)}}@media screen and (max-width:2048px){body{background-image:url(/assets/warrior_2048.jpg)}}@media screen and (max-width:1280px){body{background-image:url(/assets/warrior_1280.jpg)}}@media screen and (max-width:960px){body{background-image:url(/assets/warrior_960.jpg)}}.title-font{font-family:Uncial Antiqua}.sub-font{font-family:Major Mono Display}.inline-block{display:inline-block}.text-center{text-align:center}#root{width:61.8%;background-color:rgba(0,0,0,.3);padding:2rem 1rem}.title{font-size:5rem;letter-spacing:.1em;margin:0}.subtitle{margin:1.5rem 0 0}.desc{font-size:1.5rem}.link{padding:.3rem 1rem;transition:background .3s ease-out;cursor:pointer;background-color:#fff;color:#111;border:2px solid transparent;text-decoration:none}.link:hover{border:2px solid #fff;background-color:inherit;color:#fff}@media screen and (max-width:767px){.title{font-size:3.5rem}.desc{display:flex;flex-direction:column}.comingsoon{position:relative;right:-1em}.link{margin-top:1rem}}